import { useRouter } from "next/router";
import { useCssStateManager } from "../../hooks/use_css_state_manager";
import useAsyncEffect from "use-async-effect";
import CenteredSpinner from "../../components/common/centered-spinner";
import { apiEditOrder } from "../../lib/api/editorder";
import { useSmartState } from "../../hooks/use_smart_state";
import { cart } from "../../lib/services/cart/cart";
import { prebuildData } from "../../lib/prebuild-data";

export type LandingPreviewProps = {
};

export default function LandingPreview({}:LandingPreviewProps)
{
    const reactRouter = useRouter();

    useAsyncEffect(async (isMounted) => {        
        const urlSearchParams = new URLSearchParams(window.location.search);
        const params = Object.fromEntries(urlSearchParams.entries());        
        const preview_id = params["preview_id"];
        const loc = params["loc"];
        await prebuildData.loadOnlineSnapshot(preview_id,true);
        if (!isMounted()) return;
        reactRouter.push(loc);
    },[]);

    const cssStateManager = useCssStateManager(["landing-preview"]);
    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <CenteredSpinner usePixels scale={1.3}/>
            </div>
            <style jsx>{`
            .landing-preview {
                padding-top: 20px;
            }
            `}</style>
        </>
    );
}